// Global Style
body .wp-block-zolo-container > .zolo-container-inner-blocks-wrap,
body .wp-block-zolo-container > .zolo-container-inner-blocks-wrap > *,
body .wp-block-zolo-container > *:not(.wp-block-zolo-container):not(.wp-block) {
    min-width: unset !important;
    // position: relative;
    width: 100%;
}

