// block style
.wp-block-zolo-container {
    &.zolo-root-container {
        margin-block-start: 0;
        margin-block-end: 0;
    }

    box-sizing: border-box;
    figure {
        margin: 0;
    }
    // .block-editor-block-list__layout
    .block-editor-block-list__layout {
        position: unset;
    }
    //  alignwide css
    &.alignwide {
        max-width: 1200px;
        margin-left: auto;
        margin-right: auto;
    }
}

.wp-block-zolo-container.frontend {
    display: flex;
    // position: relative;
    box-sizing: border-box;

    &.alignfull.zolo-root-container {
        .zolo-container-inner-blocks-wrap {
            display: flex;
            // position: relative;
            box-sizing: border-box;
            margin-left: auto !important;
            margin-right: auto !important;
            width: 100%;
        }
    }
}

.zolo-container-variation-picker .block-editor-block-variation-picker__variations.block-editor-block-variation-picker__variations > li {
    margin: 0 5px;
}

.zolo-container-width-type {
    width: 100%;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    .zolo-container-btn {
        white-space: nowrap;
        display: flex;
        align-items: center;
        gap: 8px;
        padding: 6px;
        height: auto;
        min-height: auto;
    }
    .c-icon-labels {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    .c-icon svg {
        width: 30px;
        height: 30px;
        display: block;
    }

    .c-icon-info {
        color: #757575;
        font-size: 12px;
        margin-top: 4px;
    }
}

// container top toolbar width css style
.wp-block-zolo-container {
    &.zolo-root-container {
        &.zolo-block {
            &.cw_wide {
                max-width: 1280px !important;
                margin-left: auto;
                margin-right: auto;
                .zolo-container-inner-blocks-wrap {
                    max-width: 1200px !important;
                }
            }
            &.cw_full {
                max-width: unset;
                // margin-left: calc(-50vw + 50%);
                // margin-right: calc(-50vw + 50%);
                .zolo-container-inner-blocks-wrap {
                    max-width: unset;
                }
            }
        }
    }
}

.zolo-frontend {
    .wp-block-zolo-container {
        &.zolo-root-container {
            &.zolo-block {
                &.cw_full {
                    margin-left: calc(-50vw + 50%) !important;
                    margin-right: calc(-50vw + 50%) !important;
                }
            }
        }
    }
}
